  <main class="page__content">
    <ContentHeader
      v-bind:title="content_header.title"
      v-bind:subTitle="content_header.subTitle"
    />
    <div class="page__body">
      <div class="">
        <label class="form__label">Organization Logo</label> <br />
        <div class="row mr-5 h-100 align-items-center">
          <div class="form__item col-md-3 text-center">
            <div class="image-box">
              <ion-icon
                class="icon"
                v-if="!isLogoFile"
                name="image-outline"
              ></ion-icon>
              <img
                :src="logoImg"
                alt="logo"
                v-if="isLogoFile"
                class="logoPreview"
                srcset=""
              />
            </div>
            <input
              id="logoFile"
              type="file"
              accept="image/*"
              @change="onLogoFileChange($event)"
              hidden
            />
          </div>

          <div class="form__item col-md-3 text-left">
            <button
              class="form__button form__button--lg mb-3"
              type="file"
              id="myfile"
              name="myfile"
              @click="callFileUpload('logoFile')"
              v-if="!isLogoFile"
            >
              Select Image
              <ion-icon class="ml-3" name="expand-outline"></ion-icon>
            </button>
            <button
              class="form__button form__button--lg mb-3"
              type="file"
              id="myfile"
              name="myfile"
              @click="uploadLogo"
              v-if="isLogoFile"
            >
              Click to Upload <i class="ki ki-long-arrow-up icon-sm ml-3"> </i>
            </button>
            <button
              class="form__button form__button--lg mb-3 btn-danger"
              type="file"
              id="myfile"
              name="myfile"
              @click="removeLogoFile()"
              v-if="isLogoFile"
            >
              Remove Image
              <ion-icon class="ml-3" name="trash-outline"></ion-icon>
            </button>

            <!-- <label for="file-upload" class="custom-file-upload">
                                <i class="fa fa-cloud-upload"></i> Custom Upload
                            </label>
                            <input id="file-upload" class="image-input" type="file"/> -->
          </div>
        </div>
      </div>

      <!-- <hr class="dashed"> -->
      <div class="mt-5">
        <label class="form__label">Organization Fav ICON</label> <br />
        <div class="row mr-5 h-100 align-items-center">
          <div class="form__item col-md-3 text-center">
            <div class="image-box2">
              <ion-icon
                class="icon"
                v-if="!isFavFile"
                name="image-outline"
              ></ion-icon>
              <img
                :src="favImg"
                alt="fav"
                v-if="isFavFile"
                class="logoPreview"
                srcset=""
              />
            </div>
            <input
              id="favFile"
              type="file"
              accept="image/*"
              @change="onFavFileChange($event)"
              hidden
            />
            <!-- <img src="assets/img/user.png" width="100px" alt="user" srcset="" /> -->
          </div>

          <div class="form__item col-md-3 text-left">
            <button
              class="form__button form__button--lg mb-3"
              type="file"
              id="myfile"
              name="myfile"
              @click="callFileUpload('favFile')"
              v-if="!isFavFile"
            >
              Select Image
              <ion-icon class="ml-3" name="expand-outline"></ion-icon>
            </button>
            <button
              class="form__button form__button--lg mb-3"
              type="file"
              id="myfile"
              name="myfile"
              @click="uploadFav"
              v-if="isFavFile"
            >
              Click to Upload <i class="ki ki-long-arrow-up icon-sm ml-3"> </i>
            </button>
            <button
              class="form__button form__button--lg mb-3 btn-danger"
              type="file"
              id="myfile"
              name="myfile"
              @click="removeFavFile()"
              v-if="isFavFile"
            >
              Remove Image
              <ion-icon class="ml-3" name="trash-outline"></ion-icon>
            </button>

            <!-- <button  class="form__button form__button--lg" type="file" id="myfile" name="myfile" >
                            Click to Upload <i class="ki ki-long-arrow-up icon-sm ml-3"> </i>
                             </button>  -->

            <!-- <label for="file-upload" class="custom-file-upload">
                                <i class="fa fa-cloud-upload"></i> Custom Upload
                            </label>
                            <input id="file-upload" class="image-input" type="file"/> -->
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import Vue from "vue";
import "boxicons";
import organisationBrandingService from "@/core/services/general-setup-service/organisation-branding.service.js";
export default Vue.extend({
  name: "organization-branding",
  data: function() {
    return {
      content_header: {
        title: "Branding",
        subTitle:
          "Set up your organizations's logo and favicon to show on documents and other relevant places."
      },
      isLoading: false,
      orgBranding: {
        IconImage: "",
        LogoImage: ""
      },
      logoFileData: null,
      isLogoFile: false,
      logoImg: null,
      favFileData: null,
      isFavFile: false,
      favImg: null
    };
  },
  methods: {
    async getOrgBranding() {
      try {
        this.isLoading = true;
        const res = await organisationBrandingService.get();
        if (res.data.data != null) {
          this.orgBranding = res.data.data;
        }
        this.isLoading = false;
      } catch (e) {
        const error = e.response.data.message;
        alert(error);
      }
    },
    saveForm: async function(e) {
      e.preventDefault();
      try {
        this.isLoading = true;
        await organisationBrandingService.update(this.orgBranding);
        this.isLoading = false;
      } catch (e) {
        const error = e.response.data.message;
        alert(error);
      }
    },
    callFileUpload(id) {
      let input = document.getElementById(id);
      input.click();
    },
    onLogoFileChange(ev) {
      this.logoFileData = ev.target.files[0];
      const reader = new FileReader();
      reader.readAsDataURL(this.logoFileData);
      reader.onload = e => {
        this.logoImg = e.target.result;
        this.isLogoFile = true;
      };
    },
    onFavFileChange(ev) {
      this.favFileData = ev.target.files[0];
      const reader = new FileReader();
      reader.readAsDataURL(this.favFileData);
      reader.onload = e => {
        this.favImg = e.target.result;
        this.isFavFile = true;
      };
    },
    removeLogoFile() {
      this.isLogoFile = false;
      this.logoFileData = null;
      this.logoImg = null;
    },
    removeFavFile() {
      this.isFavFile = false;
      this.favFileData = null;
      this.favImg = null;
    },
    uploadLogo: async function() {
      try {
        this.isLoading = true;
        const image = new FormData();
        image.append("name", "organizationLogo");
        image.append("LogoImage", this.logoFileData);
        await organisationBrandingService.update(image);
        this.removeLogoFile();
        this.isLoading = false;
      } catch (e) {
        const error = e.response.data.message;
        alert(error);
      }
    },
    uploadFav: async function() {
      try {
        this.isLoading = true;
        const image = new FormData();
        image.append("name", "organizationIcon");
        image.append("IconImage", this.favFileData);
        await organisationBrandingService.update(image);
        this.removeLogoFile();
        this.isLoading = false;
      } catch (e) {
        const error = e.response.data.message;
        alert(error);
      }
    }
  },
  async created() {
    this.getOrgBranding();
  }
});
</script>

